$(".analysis-select").change(function () {
    let val = $(this).val()

    let renderElement = `
        <div class="selected-analysis__item">
            <span>
                ${val}
            </span>
            <button class="selected-analysis__btn">
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M18 6L6 18" stroke="white" stroke-width="3" stroke-linecap="round" stroke-linejoin="round"/>
                    <path d="M6 6L18 18" stroke="white" stroke-width="3" stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
            </button>
        </div>
    `

    $(".selected-analysis").append(renderElement)

    $(".selected-analysis__btn").click(function () {
        $(this).parents(".selected-analysis__item").remove()
    })
})
